"use strict";
import LogoProvider from "components/Logo/LogoProvider";
import { useRef, useState } from "react";
import { Flex, Input, Text } from "ui/src";
export default function AmoutInput() {
  const inputRef = useRef(null);
  const [value, setValue] = useState("");
  const handleChange = (text) => {
    const numericValue = text.replace(/[^0-9.]/g, "");
    setValue(numericValue);
  };
  return <Flex
    row
    $sm={{}}
    mt={"$gap24"}
    px="$spacing16"
    py="$spacing16"
    borderWidth={1}
    borderColor={"$surface3"}
    justifyContent="space-between"
    alignItems="center"
    gap={"$gap12"}
    borderRadius={"$rounded12"}
    tabIndex={0}
    onPress={() => inputRef.current?.focus()}
    cursor="ibeam"
    focusWithinStyle={{ borderColor: "$neutral2" }}
    transition="border-color 0.3s ease"
  >
      <Flex
    backgroundColor={"$slate"}
    py={"$padding16"}
    px={"$padding20"}
    borderRadius={"$rounded12"}
    row
    gap={"$gap8"}
    alignItems="center"
  >
        <LogoProvider size={20} /> <Text variant="buttonLabel2">ABX</Text>
      </Flex>
      <Flex grow flexDirection="column" gap={"$gap8"}>
        <Input
    ref={inputRef}
    py={"$padding20"}
    textAlign="right"
    placeholder="0"
    fontSize={"28px"}
    backgroundColor={"none"}
    width={"100%"}
    inputMode="numeric"
    keyboardType="numeric"
    value={value}
    onChangeText={handleChange}
  />
        <Text variant="body3" color={"$neutral3"} textAlign="right">
          ~$0
        </Text>
      </Flex>
    </Flex>;
}
